import React from "react";
import "../util"
import { Settings } from "./settings";
import { Board } from "./board";
import { useGame } from "src/hooks/game"

export function Game() {
   const {
      gridSize,
      mineCount,
      board,
      resetBoard,
      handleCellClick,
      handleCellRightClick,
      getGameState
   } = useGame({ initSize: 9, initMineCount: 10 });

   return (
      <div className="game" onContextMenu={(e) => e.preventDefault()}>
         <Settings
            gridSize={gridSize}
            mineCount={mineCount}
            gameState={getGameState()}
            minesLeft={mineCount - board.flagged.countBy(Boolean)}
            onSettingsUpdate={(newGridSize, newMineCount) => resetBoard(newGridSize, newMineCount)}
            onRestart={() => resetBoard(gridSize, mineCount)}
         />

         <Board
            size={gridSize}
            board={board}
            onClick={handleCellClick}
            onRightClick={handleCellRightClick}
         />
      </div>
   );
}